export const LOAD_GALLERY_POST = 'LOAD_GALLERY_POST';

export default (state = initialState, action) => {
  switch (action.type) {
    case LOAD_GALLERY_POST: {
      return {
        ...state,
      };
    }
    default: {
      return {
        ...state,
      };
    }
  }
};
